        const majorIdx = Math.floor((level - 1) / 4);
        const minorIdx = (level - 1) % 4;
        const 境界描述 = `${REALM_NAMES[majorIdx] || '练气'}${REALM_STAGES[minorIdx]}`;
        const 寿元状态 = `${data.已活岁月}/${寿元上限}`;
